*{
    margin: 0px;
    padding: 0px;
    text-decoration: none;
    font-family: sans-serif;
    
    .container{
        border: 1px solid rgba(225, 225, 226, 0.8);
        box-shadow: 0px 0px 10px #CCC;
        width: 90%;
        height: auto;
        margin: 0 auto;
        margin-top: 40px;
        margin-bottom: 40px;

        min-height: 600px;
    
        header{
            width: 100%;
            height: 120px;
            background-color: #FFFF;
            border-bottom: 1px rgba(225, 225, 226, 0.8);
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            align-items: center;
            justify-content: space-between;
    
            div{
                flex-basis: 62%;
                padding: 10px;
                margin-left: 20px;

    
                h1{
                    color: black;
                    font-family: Helvetica, sans-serif;
                    font-style: italic;
                    font-weight: 900px;
                    font-size: 35px;
                    text-shadow: 1px 1px 0px #CCC;
                }
            }
    
            nav{
                color: white;
                margin-right: 15px;

                a{
                    margin-right: 25px;
                    font-size: 15px;
                }
            }
        }
    }
}

